<template>
    <div>
        <!--  -->
        <wordpress-app v-if="_layout == conf.LAYOUT.WORDPRESS" @sign_Father="plantFinished"></wordpress-app>
        <html-app v-else-if="_layout == conf.LAYOUT.HTML" @sign_Father="plantFinished"></html-app>
        <widget-app v-else-if="_layout == conf.LAYOUT.WIDGET" @sign_Father="plantFinished_widget"></widget-app>

            <!--  -->
            <login-from-strapi 
                ref="loginREF" v-if="_layout == conf.LAYOUT.WORDPRESS"
                @defeated_Father="loginDefeated" @success_Father="loginSuccess"></login-from-strapi>

            <!--  -->
            <transition name="page_in">
                <app-view  v-if="login_success != null"></app-view>
            </transition>
    </div>
</template>

<script>
import AppView from './AppView.vue'
import HtmlApp from './components/PlantForm/HtmlApp.vue'
import WidgetApp from './components/PlantForm/WidgetApp.vue'
import WordpressApp from './components/PlantForm/WordpressApp.vue'
import LoginFromStrapi from './components/Conning/Login/LoginFromStrapi.vue'

    export default {
        components: {
            AppView,
            HtmlApp,
            WidgetApp,
            WordpressApp,
            LoginFromStrapi,   
        },
        props: {
            _layout: { type: String, default: null }
        },
        data() {
            return {
                finished: false,
                shimmer: true,
                login_success: null
            }
        },
        mounted() {

            console.log('TOKEN =', this.$store.state.token)
        },
        methods: {
            
            plantFinished(v) {
                this.finished = v
                this.$refs.loginREF.touchLogin()
                this.$emit('shimmer_Father', false)
            },
            plantFinished_widget(v) {
                this.finished = v
                this.$emit('shimmer_Father', false)
            },

            loginSuccess(v) { this.login_success = true },

            loginDefeated(typed) {
                // TRUE = 
                // FALSE = 
                if (!typed) {
                    console.log('  userID')
                } else {
                    console.log('   Strapi User')
                }

                this.login_success = false 
            },


            chronuChangeReload() {
                // this.$router.push({ path: '/home/user/loading' })
                setTimeout(e => { this.$router.push({ path: '/home/user/center' }) }, 1200)
            }
        },
    }
</script>

<style lang="sass" scoped>
    
.fade_out-leave,
.fade_out-leave-to
    transition: 1s all
    
.fade_out-leave
    opacity: 1
.fade_out-leave-to
    opacity: 0
    
    
.page_in-enter,
.page_in-enter-to
    transition: 1s all
    
.page_in-enter
    opacity: 0.3
    transform: translateY(20px)
.page_in-enter-to
    opacity: 1
    transform: translateY(0px)

</style>


